import tentacle from '../assets/tentacle.svg';
import satelite from '../assets/satelite.svg';
import ufo from '../assets/ufo.svg';
import meteor from '../assets/meteor.svg';
import TimeLine from './TimeLine';
import { langContext } from './ScreenManager';
import { useContext } from 'react';

function TimeLineScreen({ maxScroll, scroll }) {
  const isBR = useContext(langContext);
  return (
    <div>
      <img
        src={tentacle}
        style={{
          top: scroll - maxScroll + 1500 + 'px',
          left: '-200px',
          width: '55vw',
          position: 'absolute',
          transformOrigin: 'left center',
          transform:
            'rotateZ(' +
            (scroll > 3000 ? 150 - ((scroll - 3000) / 1000) * 150 : 150) +
            'deg)',
        }}
        alt=''
      ></img>
      <img
        src={ufo}
        style={{
          top: scroll - maxScroll + 2550 + 'px',
          right: '0px',
          width: '40vw',
          position: 'absolute',
          transformOrigin: 'right center',
          transform:
            'scale(' +
            (scroll > 2500 ? ( (scroll - 2500) / 600) : 0) +
            ') translateY('+(scroll > 3200 ? (scroll - 3200) : 0)+'px)',
        }}
        alt=''
      ></img>
      <img
        src={meteor}
        style={{
          top: scroll - maxScroll + 3500 + 'px',
          left: scroll > 1200 ? scroll - 1200 - 1500 + 'px' : '-1500px',
          width: '80vw',
          position: 'absolute',
          transition: 'transform 1s',
          transformOrigin: 'right center',
        }}
        alt=''
      ></img>
      <img
        src={satelite}
        style={{
          top: scroll - maxScroll + 4600 + 'px',
          right: scroll > 200 ? scroll - 200 - 1000 + 'px' : '-1500px',
          width: '40vw',
          position: 'absolute',
          transition: 'transform 1s',
          transformOrigin: 'right center',
          transform: scroll > 1200 ? '' : '',
        }}
        alt=''
      ></img>
      <TimeLine
        maxScroll={maxScroll}
        scroll={scroll}
        items={[
          {
            text: isBR
              ? 'Atuo como desenvolvedor de aplicativos hbridos com Flutter, onde crio widgets personalizados, publico pacotes que apoiam a comunidade, lido com a persistncia de dados e gerenciadores de estado. J percorri todo o processo de criao de um aplicativo do zero, incluindo a integrao com o Firebase, a implementao de recursos de mapas, grficos avanados, bssolas e visualizaes grficas complexas, at chegar  publicao oficial nas lojas de aplicativos. Alm de meu trabalho com Flutter, atuo ativamente com Node.js, onde desenvolvo REST APIs robustas e servidores WebSocket. Estes sistemas gerenciam tanto geolocalizao quanto mensageria, cruzando dados em tempo real para todos os usurios, e com persistncia em bancos de dados no relacionais MongoDB, alm de garantir a integridade dos dados entre microservios independentes com RabbitMQ. Tambm lido com o processamento de grandes volumes de dados, implementando algoritmos de recomendao e anlise orientados a grafos. Tenho, assim, uma grande e slida experincia full stack, alm de uma grande vivncia com diferentes funcionalidades e implementaes que podem ser comuns a diversos projetos.'
              : "I work as a hybrid app developer with Flutter, where I create custom widgets, publish packages that support the community, handle data persistence, and manage state. I've gone through the entire process of creating an app from scratch, including integration with Firebase, implementing features like maps, advanced graphics, compasses, and complex graphical visualizations, all the way to official publication on app stores. In addition to my work with Flutter, I actively work with Node.js, where I develop robust REST APIs and WebSocket servers. These systems manage both geolocation and messaging, cross-referencing real-time data for all users, with data persistence in non-relational MongoDB databases, while ensuring data integrity between independent microservices using RabbitMQ. I also deal with processing large volumes of data, implementing recommendation and graph-oriented analysis algorithms. As a result, I have a significant and solid full-stack experience, along with extensive exposure to various features and implementations that are common in multiple projects.",
          },
          {
            text: isBR
              ? 'Trabalhei com sistemas embarcados, desenvolvendo solues inovadoras que incluram o planejamento e criao de protocolos de comunicao do absoluto zero. Alm disso, integrei com dispositivos mveis Android via Bluetooth, implementei processamento paralelo para otimizar o desempenho, tratei mltiplas conexes e realizei a ponte de dados. Realizei tambm o controle e gerenciamento de mquinas de estados de embarcados e a persistncia e carregamento de dados complexos atravs de XML. Executei essas tarefas em linguagens como C ANSI, C++, Java e C#. E, por fim, desenvolvi aplicaes desktop com .NET e WinForms, focando no gerenciamento de testes fsicos automatizados por meio de conexes com portas seriais. Essas experincias diversificadas me proporcionaram um conjunto slido de habilidades tcnicas e a capacidade de enfrentar desafios complexos em diversas plataformas.'
              : 'I worked with embedded systems, developing innovative solutions that included planning and creating communication protocols from scratch. In addition, I integrated with Android mobile devices via Bluetooth, implemented parallel processing to optimize performance, handled multiple connections, and bridged data. I also managed and controlled embedded state machines and the persistence and loading of complex data through XML. I performed these tasks in languages such as C ANSI, C++, Java, and C#. Finally, I developed desktop applications with .NET and WinForms, focusing on the management of automated physical tests through serial port connections. These diverse experiences have provided me with a strong set of technical skills and the ability to tackle complex challenges across various platforms.',
          },
          {
            text: isBR
              ? 'Iniciei minha jornada no mundo da programao com JavaScript e desde ento trilhei um caminho diversificado e empolgante. Fiz diversos projetos, desde o desenvolvimento de jogos com mecnicas complexas, algoritmos de gerao procedural e simuladores fsicos, at o universo da inteligncia artificial, incluindo redes neurais. Tambm me dediquei  criao de motores grficos. Essas experincias aprimoraram minhas habilidades e alimentaram minha paixo por superar os limites do que pode ser alcanado no mundo da tecnologia. E, como um brinde adicional, adquiri slidos conhecimentos em HTML, CSS e TypeScript.'
              : "I began my journey in the world of programming with JavaScript and have since followed a diverse and exciting path. I've undertaken a variety of projects, from developing games with complex mechanics, procedural generation algorithms, and physics simulators, to delving into the realm of artificial intelligence, including neural networks. I've also dedicated myself to creating graphic engines. These experiences have honed my skills and fueled my passion for pushing the boundaries of what can be achieved in the world of technology. As an additional bonus, I've gained strong expertise in HTML, CSS, and TypeScript.",
          },
          {
            text: isBR
              ? 'Tenho experincia em projetos React.js e React Native, participando ativamente tanto na construo inicial quanto na manuteno contnua de aplicativos. Sou capaz de criar interfaces de usurio criativas e altamente envolventes (como est). Alm disso, integro sistemas de pagamento, lido com questes relacionadas ao fuso horrio e fao suporte de traduo de contedo em vrios idiomas. Tambm aplico metodologias geis, como o Scrum, gerenciando tarefas e projetos com eficincia atravs do uso do Jira e garantindo controle de verses com o Git.'
              : 'I have experience in React.js and React Native projects, actively participating in both the initial development and ongoing maintenance of applications. I am capable of creating creative and highly engaging user interfaces (as it is). Additionally, I integrate payment systems, handle issues related to time zones, and provide translation support for content in multiple languages. I also apply agile methodologies like Scrum, efficiently managing tasks and projects using Jira while ensuring version control with Git.',
          },
        ]}
      ></TimeLine>
    </div>
  );
}

export default TimeLineScreen;
